.burger__button {
  position: relative;
  z-index: 40;
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.burger__button span {
  position: absolute;
  top: 50%;
  left: calc(50% - 11px);
  width: 22px;
  height: 2px;
  background-color: var(--color-black, #000);
  transform: translate(0, -50%);
  transition: all 0.5s;
}
.burger__button span:nth-of-type(2) {
  top: calc(50% - 7px);
}
.burger__button span:nth-of-type(3) {
  top: calc(50% + 7px);
}
[data-state=mobile-menu] .burger__button--active span:nth-of-type(1) {
  display: none;
}
[data-state=mobile-menu] .burger__button--active span:nth-of-type(2) {
  top: 50%;
  transform: rotate(45deg);
}
[data-state=mobile-menu] .burger__button--active span:nth-of-type(3) {
  top: 50%;
  transform: rotate(-45deg);
}
